import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Geolocation } from '@ionic-native/geolocation/ngx';
import { ModalController, Platform } from '@ionic/angular';
import { Diagnostic } from '@ionic-native/diagnostic/ngx';
// import { GaoDeLocation } from '@ionic-native/gao-de-location/ngx';
import { EventService } from '../../shared/service/event.service';
import { CityCode } from '../../../assets/data/CityCode';
import { HttpService } from './http.service';
declare var cordova: any;
declare var BMap;
@Injectable({
    providedIn: 'root'
})
export class MapBaiduService {

    constructor(
        private http: HttpClient, public geolocation: Geolocation, private httpService: HttpService,
        private modalCtrl: ModalController,
        private platform: Platform, private eventService: EventService,
        private diagnostic: Diagnostic,
        // private gaoDeLocation: GaoDeLocation
    ) { }

    localCityName: string;
    headers: any;

    lat: any;
    long: any;
    locationObj = { center: { lng: 114.066112, lat: 22.548511 }, name: '深圳', code: 4403 };
    ionViewDidLoad(callbackFunc?: Function) {
        const that = this;
        const myCity = new BMap.LocalCity();
        myCity.get((result) => {
            result.name = JSON.stringify(result.name).replace('市', '').replace('\"', '').replace('\"', '');
            result.code = that.getCityId(result.name);
            that.setStore('currentCityPosition', result);
            that.setStore('position', result);
            callbackFunc();
        });
    }

    getGPSStatu(scallback, fcallback) {
        this.diagnostic.isLocationAvailable()
            .then((availible) => {
                console.log(availible, 'available');
                if (availible) {
                    this.ionViewDidLoad();
                } else {
                    fcallback();
                }
            }).catch(e => console.error(e));
    }

    getCityId(name) {
        const city = new CityCode();
        const len = city.length;
        let id = 4403;
        for (let i = 0; i < len; i++) {
            if (name === city[i].n) {
                id = city[i].c;
                break;
            }
        }
        return id;
    }




    /**
     * 通过城市名称或者区域返回中心经纬度 可传区域模糊查询
     * @param cityName 必填
     * @param districtName 选填
     */
    getPointByCityName(cityName, districtName?) {
        const that = this;
        const myGeo = new BMap.Geocoder();
        myGeo.getPoint((districtName !== undefined ? cityName + districtName : cityName), (point) => {
            if (point) {
                localStorage.setItem('tempPoint', JSON.stringify(point));
                that.eventService.eventEmit.emit('storePoint', '临时存储经纬度信息');
            }
        }, cityName);
    }

    // 调用百度SDK获取定位信息
    refreshWatchPosition(callbackFunc?) {
        const that = this;
        that.platform.ready().then(() => {
            if (that.platform.is('cordova')) {// app端
                cordova.plugins.baidumap_location.getCurrentPosition((res) => {
                    console.log("msg----->  location  success " + JSON.stringify(res, null, 4));
                    const code = res.adcode;
                    const cityName = res.city;
                    const points = { lng: res.longitude, lat: res.latitude };
                    that.locationObj.center = points;
                    that.locationObj.code = Number(code);
                    that.locationObj.name = cityName;
                    that.setStore('currentCityPosition', that.locationObj);
                    that.setStore('position', that.locationObj);
                    that.setStore('askForTag', 0);
                    callbackFunc();
                }, (error) => {
                    that.ionViewDidLoad(callbackFunc);
                    console.log("msg----->  location  error " + JSON.stringify(error));
                });
            } else { // 预览模式
                that.ionViewDidLoad(callbackFunc);
            }
        });
    }



    getStore(name: string) { if (!name) { return; } return window.localStorage.getItem(name); }

    setStore(name: string, content: any) {
        if (!name) { return; }
        if (typeof content !== 'string') { content = JSON.stringify(content); }
        window.localStorage.setItem(name, content);
    }

    removeStore(name: string) { if (!name) { return; } window.localStorage.removeItem(name); }

    bd_encrypt(ggLng, ggLat) {
        const X_PI = Math.PI * 3000.0 / 180.0;
        const x = ggLng, y = ggLat;
        const z = Math.sqrt(x * x + y * y) + 0.00002 * Math.sin(y * X_PI);
        const theta = Math.atan2(y, x) + 0.000003 * Math.cos(x * X_PI);
        const bdLng = z * Math.cos(theta) + 0.0065;
        const bdLat = z * Math.sin(theta) + 0.006;
        return { lat: bdLat, lng: bdLng };
    }
}
