    refreshLevelPointData(mapRef) {
        const that = this;
        that.numberCtrlElement.nativeElement.style.display = that.numberCtrlElement !== undefined ? "block" : "none";
        if (that.countCtrlElement !== undefined) { that.countCtrlElement.nativeElement.style.display = "block"; }
        that.updateAppDataMapList(that.formatPointData);
        that.refreshIndustryStateData();
        const long = parseFloat(this.localPoint.lng + '');
        const lat = parseFloat(this.localPoint.lat + '');
        const marker = new BMap.Marker(new BMap.Point(long, lat), { icon: that.locationIcon });
        mapRef.addOverlay(marker);
        const pmarks = [];
        let statePng = new BMap.Icon("/assets/img/map/11.png", new BMap.Size(30, 35));
        for (let i = 0, len = that.formatPointData.infoList.length; i < len; i++) {
            const idx = (that.formatPointData.infoList[i].industryType + '').substring(0, 2);
            const url = "/assets/img/map/" + idx;
            statePng = new BMap.Icon(url + ".png", new BMap.Size(30, 35));
            if (that.formatPointData.infoList[i].longitude !== undefined
                && that.formatPointData.infoList[i].latitude !== undefined) {
                pmarks[i] = new BMap.Marker(
                    new BMap.Point(that.formatPointData.infoList[i].longitude,
                        that.formatPointData.infoList[i].latitude),
                    { icon: statePng });
                pmarks[i].addEventListener("click", e => {
                    if (that.shopMark !== undefined) {
                        if (pmarks[that.shopMark.i] !== undefined) {
                            const purl = pmarks[that.shopMark.i].getIcon().imageUrl.replace("_0", "_1");
                            pmarks[that.shopMark.i].setIcon(new BMap.Icon(purl, new BMap.Size(30, 35)));
                            that.formatPointData.infoList[that.shopMark.i].stClick = undefined;
                        }
                    }
                    that.shopMark = pmarks[i];
                    that.shopMark.i = i;
                    if (that.formatPointData.infoList[i].stClick !== undefined) {
                        pmarks[i].setIcon(new BMap.Icon(url + ".png", new BMap.Size(30, 35)));
                        document.getElementById('shopDetailCtrl').style.display = "none";
                        that.formatPointData.infoList[i].stClick = undefined;
                    } else {
                        that.formatPointData.infoList[i].stClick = '0';
                        pmarks[i].setIcon(new BMap.Icon(url + "_0.png", new BMap.Size(30, 35)));
                        document.getElementById('subTabsCtrl-div3').style.display = "none";
                        document.getElementById('shopDetailCtrl').style.display = "block";
                        document.getElementById("detailInfoTitle").innerHTML = that.formatPointData.infoList[i].title;
                        document.getElementById("detailInfoExtend").innerHTML = that.formatPointData.infoList[i].info;
                        document.getElementById("detailInfoPoints").innerHTML
                            = that.formatPointData.infoList[i].points === undefined ?
                                "未知" : that.formatPointData.infoList[i].points;
                        document.getElementById("detailInfoRent").innerHTML = that.formatPointData.infoList[i].rent;
                        document.getElementById("detailInfoTimes").innerHTML = that.formatPointData.infoList[i].times;
                        const targetObj = {
                            pageId: that.formatPointData.infoList[i].link.target,
                            params: that.formatPointData.infoList[i].link.params,
                            sessionId: localStorage.getItem('sessionId')
                        };
                        document.getElementById("detailInfo_targetObj").innerHTML = JSON.stringify(targetObj);
                    }
                });
                mapRef.addOverlay(pmarks[i]);
            }
        }
    }
