import { Component, OnInit, Input } from '@angular/core';
import { AlertController, ActionSheetController } from '@ionic/angular';
import { AppGlobalService } from '../../shared/service/app-global.service';
@Component({
  selector: 'app-member-status',
  templateUrl: './member-status.component.html',
  styleUrls: ['./member-status.component.scss'],
})
export class MemberStatusComponent implements OnInit {

  @Input() tagData: any;

  constructor(
    private appGlobalService: AppGlobalService,
    private alert: AlertController,
    private actionSheetCtrl: ActionSheetController, ) { }

  ngOnInit() {}

  gotoDetail(obj) {
    const targetParams = {
      pageId: obj.link.target,
      params: obj.link.params,
      sessionId: localStorage.getItem('sessionId')
    };
    this.appGlobalService.goToForward(targetParams);
  }

  async askPauseOrStop(manageServiceData) {
    const that = this;
    const actionSheet = await this.actionSheetCtrl.create({
      mode: 'md',
      cssClass: 'as_padding as_common as_border_common',
      buttons: [{
        text: '' + manageServiceData[0].label,
        cssClass: 'as_font_bd',
        handler: () => {
          that.gotoDetail(manageServiceData[0]);
        }
      }, {
        text: '' + manageServiceData[1].label,
        cssClass: 'as_font_color',
        handler: () => {
          that.gotoDetail(manageServiceData[1]);
        }
      }]
    });
    await actionSheet.present();
  }

  async askAbout(phone, tips) {
    const alert = await this.alert.create({
      message: tips,
      buttons: [
        {
          text: '取消',
          role: 'cancel',
          cssClass: 'secondary',
          handler: () => {
            console.log('Confirm Cancel');
          }
        }, {
          text: '确定',
          handler: () => {
            window.open('tel:' + phone);
          }
        }
      ]
    });
    await alert.present();
  }
}
