  sendMessage() {
    const msg = $msg({
      to: 'newuser53@xw-im',
      from: 'stone@xw-im',
      type: 'chat'
    }).c("body", null, this.message_text);
    this.connection.send(msg.tree());
    this.messages.push({from: this.nickname, text: this.message_text});
    this.message_text = '';
  }
