import { Component, Input, OnInit } from '@angular/core';
import { AppGlobalService } from '../../shared/service/app-global.service';
@Component({
  selector: 'app-footer-protocol',
  templateUrl: './footer-protocol.component.html',
  styleUrls: ['./footer-protocol.component.scss'],
})
export class FooterProtocolComponent implements OnInit {

  @Input() tagData: any;

  constructor(private appGlobalService: AppGlobalService) { }

  ngOnInit() { }

  gotoProtocol(obj) {
    const targetObj = { pageId: obj.target, params: obj.params, sessionId: localStorage.getItem('sessionId') };
    this.appGlobalService.goToForward(targetObj);
  }
}
