import {Component, OnInit, AfterViewInit} from '@angular/core';
import {ModalController, NavParams} from '@ionic/angular';
import {Router} from '@angular/router';
import {AppGlobalService} from '../../shared/service/app-global.service';
import {FormGroup, FormControl, Validators, FormGroupDirective} from '@angular/forms';

@Component({
    templateUrl: 'textArea.page.html',
    styleUrls: ['post-and-eitor.component.scss']
})
export class textAreaPage implements OnInit, AfterViewInit {
    data: any;
    textAreaForm: FormGroup = new FormGroup({});
    hiddenLabel:any={};

    constructor(private navParams: NavParams,
                private modalCtrl: ModalController,
                public router: Router,
                private appGlobalService: AppGlobalService) {
    }

    ngOnInit() {
        this.data = this.navParams.data.value;
        let thisValue = this.data.item.value?(this.data.item.value.substr(0, 2).indexOf('#') > -1? '' : this.data.item.value):'' ;
        this.textAreaForm.addControl(this.data.item.field, new FormControl(thisValue,Validators.maxLength(this.data.item.maxLength)));
    }

    ngAfterViewInit() {}

    dismiss() {
        if(this.textAreaForm.invalid){
            this.appGlobalService.showToast(`请输入正确的${this.data.item.label}`, 3000, 'top') 
            return;
        }
        this.modalCtrl.dismiss(this.textAreaForm);
    }

}