import { Component, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';
import { ModalController, NavParams } from '@ionic/angular';
import {MapShowComponent} from '../map-show/map-show.component'

@Component({
  selector: 'app-address-map',
  templateUrl: './address-map.component.html',
  styleUrls: ['./address-map.component.scss']
})
export class AddressMapComponent implements OnInit {

  @Input()
  tagData:any;

  constructor(
    private router: Router,
    public modalCtrl: ModalController
  ) { }

  ngOnInit() {
    console.log(this.tagData)
  }

 async openModal(){
    let mapModal = await this.modalCtrl.create({
      component: MapShowComponent,
      componentProps: { value: JSON.stringify({lng:116.404, lat:39.915})} 
    })
    // 116.404, 39.915
    // lng:this.tagData.longitude, lat:this.tagData.latitude
    
    return await mapModal.present();
  }

}
