import { Component, OnInit, Input } from '@angular/core';
import { FormGroup, Validators } from '@angular/forms';

import { ModalController } from '@ionic/angular';
import { ActionSheetController } from '@ionic/angular';

import { EditNickNameComponent } from '../edit/editnickname.page';
import { UploadImageComponent } from '../upload-image/upload-image.component';
import { ChooseCityComponent } from '../../coms/choose-city/choose-city.component';
import { CustomValidators } from '../../shared/custom-validators/custom-validators';
import { EventService } from '../../shared/service/event.service';
import { HttpService } from '../../shared/service/http.service';
import { MapBaiduService } from '../../shared/service/map.baiduapi';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {
  // 组件数据
  @Input() tagData: any = {};
  // 表单
  tempImg: any = [];
  img: any;   // 图片显示
  // 默认头像
  defaulticonsrc = '../../assets/img/headtrait.png';
  form: FormGroup = new FormGroup({});
  gender = { name: '男', value: 0 };
  // 位置对象
  positionInfo: any = {};
  nickname: any;
  cityName: any;
  picpath = '../../assets/img/headtrait.png';
  constructor(
    private modalCtrl: ModalController,
    private http: HttpService,
    private mapBaiduService: MapBaiduService,
    private actionSheetCtrl: ActionSheetController,
    private eventService: EventService
  ) {
    this.positionInfo = JSON.parse(this.mapBaiduService.getStore('currentCityPosition'));
  }
  avator_id: any;
  nickname_id: any;
  gender_id: any;
  city_id: any;
  user_id: any;
  userInfo = {
    avatar_url: '../../assets/img/headtrait.png',
    gender: -1,
    nickname: '',
    city_id: 4403
  };
  cityname: any;
  ngOnInit() {

    setTimeout(() => {
      this.img = [];
      this.tagData['data'].map(item => {
        if (item['htmlStyle'] === 'input') {
          this.nickname = item['value'];
          this.userInfo.nickname = this.nickname;
          this.nickname_id = item['button'].id;
        } else if (item['htmlStyle'] === 'image') {
          this.avator_id = item['button'].id;
          if (item !== undefined && item['value'] !== undefined) {
            this.picpath = item['value'] === '' ? this.defaulticonsrc : (item['value'] + '');
            this.img[0] = { name: 'upload.jpg', path: this.picpath };
            this.userInfo.avatar_url = item['value'];
          }
        } else if (item['htmlStyle'] === 'radio') {
          this.gender_id = item['button'].id;
          this.gender = { name: item['value'] === '0' ? '男' : item['value'] === '1' ? '女' : '未知', value: item['value'] };
          this.userInfo.gender = item['value'];
        } else if (item['htmlStyle'] === 'text') {
          this.city_id = item['button'].id;
          if (item !== undefined && item['value'] !== undefined) {
            
            this.positionInfo.code = String(item['value']).split(',')[1];
            this.userInfo.city_id = this.positionInfo.code;
            this.positionInfo.name = String(item['value']).split(',')[0];
            // this.getCityNameById(item['value']);
            this.cityName = this.positionInfo.name;
          }
        }
      });
      localStorage.setItem('userInfo', JSON.stringify(this.userInfo));
    }, 100);
  }

  // getCityNameById(code) {
  //   const city = new CityCode();
  //   const len = city.length;
  //   for (let i = 0; i < len; i++) {
  //     if (code === (city[i].c + '')) {
  //       this.cityName = city[i].n;
  //       break;
  //     }
  //   }
  //   return name;
  // }

  /**
   * 修改头像
   */
  async openImageModal() {
    const that = this;
    const modal = await this.modalCtrl.create({
      component: UploadImageComponent,
      componentProps: { value: JSON.stringify(this.img), imgCount: 1 }
    });
    modal.onDidDismiss().then(function (data) {
      that.img = data.data;
      if (data.data !== undefined && data.data.length > 0) {
        that.picpath = that.img[0].path;
      } else {
        that.picpath = '../../assets/img/headtrait.png';
      }
      that.form.value['avatar_url'] = that.picpath;
      const tmpUserInfo = JSON.parse(localStorage.getItem('userInfo'));
      tmpUserInfo.avatar_url = that.picpath;
      localStorage.setItem('userInfo', JSON.stringify(tmpUserInfo));
      that.formSubmit(that.avator_id);
    });
    return await modal.present();
  }

  /**
   * 选择性别
   */
  async selectSex(type?) {
    const actionSheet = await this.actionSheetCtrl.create({
      mode: 'md',
      cssClass: 'match-padding match-padding-common firstchild lastchild',
      buttons: [{
        text: '男',
        cssClass: type === 0 ? 'action-sheet-font-color-red' : 'action-sheet-font-color',
        handler: () => {
          this.form.value['sex'] = 0;
          this.gender = { name: '男', value: 0 };
          const tmpUserInfo = JSON.parse(localStorage.getItem('userInfo'));
          tmpUserInfo.gender = 0;
          localStorage.setItem('userInfo', JSON.stringify(tmpUserInfo));
          this.formSubmit(this.gender_id);
        }
      }, {
        text: '女',
        cssClass: type === 1 ? 'action-sheet-font-color-red' : 'action-sheet-font-color',
        handler: () => {
          this.form.value['sex'] = 1;
          this.gender = { name: '女', value: 1 };
          const tmpUserInfo = JSON.parse(localStorage.getItem('userInfo'));
          tmpUserInfo.gender = 1;
          localStorage.setItem('userInfo', JSON.stringify(tmpUserInfo));
          this.formSubmit(this.gender_id);
        }
      }]
    });
    await actionSheet.present();
  }

  /**
   * 选择城市
   */
  async selectCity(target) {
    const that = this;
    const modal = await this.modalCtrl.create({
      component: ChooseCityComponent,
      componentProps: { value: target, modalTitle: '选择城市' }    // 首页是1 默认不传或者0
    });
    modal.onDidDismiss().then(function (data) {
      // console.log('message----->onDidDismiss:=' + JSON.stringify(data));
      const value = data.data;
      if (value.n !== undefined && value.n !== '') {
        that.form.value['city_id'] = value.i;
        const tmpUserInfo = JSON.parse(localStorage.getItem('userInfo'));
        tmpUserInfo.city_id = value.i;
        localStorage.setItem('userInfo', JSON.stringify(tmpUserInfo));
        that.cityName = value.n;
        that.positionInfo.name = value.n;
        that.positionInfo.code = value.i;
        that.formSubmit(that.city_id);
      }
    });
    return await modal.present();
  }

  /**
   * 修改昵称
   * @param name
   */
  async gotoModifyName(name) {
    const that = this;
    const modal = await this.modalCtrl.create({
      component: EditNickNameComponent,
      componentProps: { value: name }
    });
    modal.onDidDismiss().then(function (data) {
      const value = data.data;
      if (value !== undefined) {
        that.form.value['nickname'] = value;
        const tmpUserInfo = JSON.parse(localStorage.getItem('userInfo'));
        tmpUserInfo.nickname = value;
        localStorage.setItem('userInfo', JSON.stringify(tmpUserInfo));
        that.formSubmit(that.nickname_id);
        that.nickname = value;
      }
    });
    return await modal.present();
  }
  /**
   * 验证
   *
   * @param {*} type 类型
   * @returns
   * @memberof EditComponent
   */
  selectVaildator(type) {
    const validators = [];
    switch (type) {
      case 'mobile':
        validators.push(Validators.required, CustomValidators.phone);
        break;
      case 'text':
        validators.push(Validators.required);
        break;
      default:
        validators.push(Validators.required);
        break;
    }
    return validators;
  }


  /**
   * 提交表单
   *
   * @memberof EditComponent
   */
  formSubmit(id) {
    if (this.form.valid) {
      const params = {
        content: this.form.value,
        id: id,
        sessionId: localStorage.getItem('sessionId')
      };
      this.http.post('application/submit', params, res => {
        if (res['success']) {
          // this.appGlobalService.showToast(res['message'], 2000, 'middle');
          // this.eventService.eventEmit.emit('refreshUserInfo', '刷新用户中心的信息');
          localStorage.setItem('needRefreshTab1', 'true');
          localStorage.setItem('needRefreshTab2', 'true');
          localStorage.setItem('needRefreshTab3', 'true');
          localStorage.setItem('needRefreshTab4', 'true');
          localStorage.setItem('needRefreshTab5', 'true');
        } else {
          // this.appGlobalService.showToast(res['message'], 2000, 'middle');
        }
      }, err => {
        console.log('err-----> err:==' + JSON.stringify(err));
      });
    }
  }
  /**
   * 将上传的图片地址赋给表单
   *
   * @param {*} event 图片地址的数组对象
   * @memberof EditComponent
   */
  imgObjChange(event) {
    this.tagData['columns'].forEach(item => {
      if (item['type'] === 'picture') {
        this.form.controls[item['name']].setValue(event[0]);
      }
    });
  }
}
