import {Component, OnInit, Input} from '@angular/core';
import {AlertController} from '@ionic/angular';

@Component({
    selector: 'app-poster-call',
    templateUrl: './poster-call.component.html',
    styleUrls: ['./poster-call.component.scss'],
})
export class PosterCallComponent implements OnInit {

    @Input() tagData: any;

    constructor(
        private alertController: AlertController
    ) {
    }

    ngOnInit() {
        /*this.tagData = {
            type: 'tag',
            style: 'xxxx',
            name: 'xxxx',
            data: {
                imageUrl: '../../../assets/img/home_banner.png',
                phone: '0755-26637688'
            }
        };*/
    }

    async call(mobile) {
        const alert = await this.alertController.create({
            header: this.tagData.data.nick_name,
            message: mobile,
            buttons: [
                {
                    text: '取消',
                    role: 'cancel',
                    cssClass: 'secondary'
                }, {
                    text: '拨打',
                    handler: () => {
                        window.location.href = 'tel://' + mobile;
                    }
                }
            ]
        });

        await alert.present();
    }

}
