import { Component, Input, OnInit, Inject, forwardRef } from '@angular/core';
import { ComItem } from '../../route/entry/com-item';
import { EventService } from '../../shared/service/event.service';
import { HttpService } from '../../shared/service/http.service';
import { TabsService } from 'src/app/shared/service/tabs.service';

@Component({
    selector: 'app-button-nav',
    templateUrl: './button-nav.component.html',
    styleUrls: ['./button-nav.component.scss']
})
export class ButtonNavComponent implements OnInit {
    subComs: ComItem[] = [];
    @Input() tagData: any;
    aWidth: string;
    isSelected: boolean;
    currentClickObj: any;
    currentCLickIndexTab: any;
    isRootURL: any; // 用于检查URL是否有变化
    constructor(
        private eventService: EventService,
        private httpService: HttpService,
        @Inject(forwardRef(() => TabsService)) public tabService: TabsService
    ) {
    }

    ngOnInit() {
        this.aWidth = 100 / this.tagData.data.length + '%';
        const cDataObj = this.tagData.currentData;
        if (cDataObj !== undefined) {
            const tags = cDataObj.tags;
            if (tags !== undefined && tags.length > 0) {
                const len = tags.length;
                for (let i = 0; i < len; i++) {
                    const tag = tags[i];
                    this.tabService.getComsDataByStyle(tag.style, callData => {
                        if (callData) {
                            this.subComs.push(callData);
                        }
                    }, tag);
                }
            }
        }
        this.reloadCurrentTab();

        // if (this.tagData.name !== 'homeAppTabTag') {
        //     this.eventService.eventEmit.on('refreshSubTabContainerForRootUrl', () => {
        //         if (localStorage.getItem("isRootURL") !== undefined && localStorage.getItem("isRootURL") != null) {
        //             this.isRootURL = localStorage.getItem("isRootURL");
        //             localStorage.removeItem("isRootURL");
        //             document.getElementById('btNavTab0').click();
        //         }
        //     });
        // }
    }
    change(obj, indexTab) {
        if (obj !== undefined && obj.partLink !== undefined) {
            this.tagData.currentTab = indexTab;
            this.httpService.post('application/part',
                {
                    viewId: obj.partLink.target,
                    sessionId: localStorage.getItem("sessionId"),
                    params: obj.partLink.params
                }, (res: any) => {
                    if (res !== undefined && res != null) {
                        setTimeout(() => {
                            this.tagData.currentData = JSON.parse(res.script);
                            // localStorage.setItem("needRefresh","true");
                            localStorage.setItem("dataMapSubContainer", JSON.stringify(this.tagData));
                            this.eventService.eventEmit.emit('refreshSubTabContainer', 'refreshSubTabContainer');
                        }, 50);

                    }
                });
        }
    }


    reloadCurrentTab() {
        this.eventService.eventEmit.on('refreshSubTab', data => {
            console.log('refreshSubTab------->');
            document.getElementById('btNavTab' + this.tagData.currentTab).click();
        });
    }
}
