import { Component, Input, OnInit, Inject, forwardRef } from '@angular/core';

import { TabsService } from 'src/app/shared/service/tabs.service';

import { ComItem } from '../../route/entry/com-item';
import { EventService } from '../../shared/service/event.service';
import { HttpService } from '../../shared/service/http.service';

@Component({
  selector: 'app-fixed-tab',
  templateUrl: './fixed-tab.component.html',
  styleUrls: ['./fixed-tab.component.scss'],
})
export class FixedTabComponent implements OnInit {
  subComs: ComItem[] = [];
  @Input() tagData: any;
  aWidth: string;
  constructor(
    private eventService: EventService,
    private httpService: HttpService,
    @Inject(forwardRef(() => TabsService)) public tabService: TabsService
  ) {
  }

  ngOnInit() {
    const cDataObj = this.tagData.currentData;
    this.reloadCurrentTab();
    if (cDataObj !== undefined) {
      const tags = cDataObj.tags;
      if (tags !== undefined && tags.length > 0) {
        const len = tags.length;
        for (let i = 0; i < len; i++) {
          const tag = tags[i];
          if (tag.linearLayout === 'body') {
            this.tabService.getComsDataByStyle(tag.style, callData => {
              if (callData) {
                this.subComs.push(callData);
              }
            }, tag);
          } else {
            localStorage.setItem("refreshFooterComsTag", JSON.stringify(tag));
            this.eventService.eventEmit.emit("testRefreshFooterComsContainer", "testRefreshFooterComsContainer");
            // this.eventService.eventEmit.emit("showFooterComsContainer", "showFooterComsContainer");
          }
        }
      }
    }
  }
  reloadCurrentTab() {
    this.eventService.eventEmit.on('refreshSubTab', data => {
      console.log('refreshSubTab------->sub');
      if (this.tagData.currentTab) {
        document.getElementById('fixedNavTab' + this.tagData.currentTab).click();
      }
    });
  }
}
