import { Injectable } from '@angular/core';
import { EventEmitter } from 'eventemitter3';

@Injectable({
    providedIn: 'root'
})
export class EventService {
    public eventEmit: any;
    constructor() {
        this.eventEmit = new EventEmitter();
    }

    emit(name, data?) {
        this.eventEmit.emit(name, data);
    }

    on(name, handler) {
        this.eventEmit.on(name, handler);
    }

    removeAllListeners(evt?) {
        this.eventEmit.removeAllListeners(evt);
    }

    off(name, handler) {
        this.eventEmit.off(name, handler);
    }

    getEventCount(): number {
        return (this.eventEmit as any)._eventsCount;
    }
}
