import { Component, Input, OnInit } from '@angular/core';

import { AppGlobalService } from '../../shared/service/app-global.service';

@Component({
    selector: 'app-block-link',
    templateUrl: './block-link.component.html',
    styleUrls: ['./block-link.component.scss']
})
export class BlockLinkComponent implements OnInit {

    @Input() tagData: any;

    aWidth: string;

    constructor(private appGlobalService: AppGlobalService) {
    }

    ngOnInit() {
        // this.tagData = {
        //     'type': 'tag',
        //     'style': 'tag.BlockTagLogic',
        //     'name': null,
        //     'colNum': 2,
        //     'data': [
        //         {
        //             'label': '转让信息',
        //             'imageUrl': '../../../assets/img/zhuanrang.png',
        //             'link': {
        //                 'type': 'page',
        //                 'target': 'page001'
        //             }
        //         },
        //         {
        //             'label': '选址信息',
        //             'imageUrl': '../../../assets/img/zhaodian.png',
        //             'link': {
        //                 'type': 'page',
        //                 'target': 'page002'
        //             }
        //         }
        //
        //     ]
        // };
        this.aWidth = 100 / this.tagData.colNum + '%';
    }

    jumpToPage(item) {
        const sessionId = localStorage.getItem('sessionId');
        const pageParams = {
            pageId: item.target,
            params: {},
            sessionId: sessionId
        };
        const cityId = JSON.parse(localStorage.getItem('position')) ? JSON.parse(localStorage.getItem('position')).code : 4403;
        if (item.param !== undefined) {
            pageParams.params[item.param[0].name] = cityId;
        }

        this.appGlobalService.goToForward(pageParams);
    }

}
