import {Component, Input, OnInit} from '@angular/core';

import {ActionSheetController, AlertController} from '@ionic/angular';

import { AppGlobalService } from '../../shared/service/app-global.service';

@Component({
    selector: 'app-call',
    templateUrl: './call.component.html',
    styleUrls: ['./call.component.scss']
})
export class CallComponent implements OnInit {

    @Input() tagData: any;

    defaultAvatar: String = '../../assets/img/head.png';

    constructor(
        private actionSheetCtrl: ActionSheetController,
        private alertController: AlertController,
        private appGlobalService: AppGlobalService
    ) {
    }

    ngOnInit() {
        console.log(this.tagData);
    }

    call() {
        if (this.tagData.data.call.enable === undefined || this.tagData.data.call.enable === true) {
            if (this.tagData.data.otherMobile) {
                this.presentAction();
            } else {
                this.presentAlertConfirm(this.tagData.data.mobile);
            }
        }
    }

    async presentAction() {
        const buttonsArr = [{
            text: '取消',
            cssClass: ['action-sheet-font-color', 'action-sheet-font-top'],
            handler: () => {
                actionSheet.dismiss();
            }
        }];
        if (this.tagData.data.otherMobile) {
            buttonsArr.unshift({
                text: this.tagData.data.otherMobile,
                cssClass: ['action-sheet-font-color'],
                handler: () => {
                    this.presentAlertConfirm(this.tagData.data.otherMobile);
                }
            });
        }

        if (this.tagData.data.mobile) {
            buttonsArr.unshift({
                text: this.tagData.data.mobile,
                cssClass: ['action-sheet-font-color'],
                handler: () => {
                    this.presentAlertConfirm(this.tagData.data.mobile);
                }
            });
        }

        const actionSheet = await this.actionSheetCtrl.create({
            mode: 'md',
            cssClass: 'match-padding',
            buttons: buttonsArr
        });
        await actionSheet.present();
    }

    async presentAlertConfirm(mobile) {
        const alert = await this.alertController.create({
            header: this.tagData.nickName,
            message: mobile,
            buttons: [
                {
                    text: '取消',
                    role: 'cancel',
                    cssClass: 'secondary'
                }, {
                    text: '拨打',
                    handler: () => {
                        window.location.href = 'tel://' + mobile;
                    }
                }
            ]
        });

        await alert.present();
    }

    go(obj) {
        const pageParams = {
            pageId: obj.target,
            params: obj.params,
            sessionId: localStorage.getItem('sessionId')
        };
        this.appGlobalService.goToForward(pageParams);
    }
}
