import { Component, OnInit, AfterViewInit } from '@angular/core';
import { ModalController, NavParams } from '@ionic/angular';
import { Router } from '@angular/router';
import { AppGlobalService } from '../../shared/service/app-global.service';
import { FormGroup, FormControl, Validators, AbstractControl, ValidatorFn } from '@angular/forms';
import { isArray } from 'util';
import { HttpService } from '../../shared/service/http.service';

@Component({
    templateUrl: 'hiddenele.page.html',
    styleUrls: ['post-and-eitor.component.scss']
})
export class hiddeneleComponent implements OnInit, AfterViewInit {
    data: any;
    FormGroup: FormGroup;
    hiddenLabel: any = {};

    constructor(private navParams: NavParams,
        private modalCtrl: ModalController,
        public router: Router,
        private http: HttpService,
        private appGlobalService: AppGlobalService) {
    }

    ngOnInit() {
        this.data = JSON.parse(this.navParams.data.value);
        console.log(this.data, 'data');
        this.FormGroup = new FormGroup({});
        for (let i = 0; i < this.data.item.itemList.length; i++) {
            const value = this.data.item.itemList[i];
            if (value.type === 'toggle') {
                value.value = (value.value === '0' || !value.value) ? false : true;
                if (value.writable) {
                    if (!value.both) {
                        this.FormGroup.addControl(`customToggle__${value.field}`, new FormControl(value.value));
                    } else {
                        this.FormGroup.addControl(value.field, new FormControl(value.value, this.validators(value)));
                    }
                    const rangeLen = value.itemObj.itemList.length;
                    for (let k = 0; k < rangeLen; k++) {
                        const itemList = value.itemObj.itemList[k];
                        // let rangeValue = itemList.value?(itemList.value.substr(0, 2).indexOf('#') > -1? '' : itemList.value):'' ;
                        this.FormGroup.addControl(value.itemObj.itemList[k].field, new FormControl(itemList.value));
                        this.hiddenLabel[value.itemObj.itemList[k].field] = { label: value.itemObj.itemList[k].label, message: value.itemObj.itemList[k].message };
                        if (value.value) {
                            this.FormGroup.get(value.itemObj.itemList[k].field).setValidators(this.validators(itemList));
                            this.FormGroup.get(value.itemObj.itemList[k].field).updateValueAndValidity();
                            // if(k===1){
                            //     this.FormGroup.get(itemList.field).setValidators(this.rangeValidator(value.itemObj));
                            // }
                        } else {
                            this.FormGroup.get(itemList.field).clearValidators();
                            this.FormGroup.get(value.itemObj.itemList[k].field).updateValueAndValidity();
                        }

                    }
                    if (value.itemObj.itemObj && value.itemObj.itemObj.field) {
                        this.FormGroup.addControl(value.itemObj.itemObj.field, new FormControl(value.itemObj.itemObj.value, this.validators(value.itemObj.itemObj)));
                        this.getPickerLabel(value.itemObj.itemObj);
                    }
                } else {
                    this.FormGroup.addControl(value.field, new FormControl(value.value, this.validators(value)));
                    this.hiddenLabel[value.field] = { label: value.label, message: value.message };
                }
            } else if (value.type === 'range') {
                const rangeLen = value.itemList.length;
                for (let k = 0; k < rangeLen; k++) {
                    const itemList = value.itemList[k];
                    // let rangeValue = itemList.value?(itemList.value.substr(0, 2).indexOf('#') > -1? '' : itemList.value):'' ;
                    this.FormGroup.addControl(value.itemList[k].field, new FormControl(itemList.value, this.validators(itemList)));
                    this.hiddenLabel[value.itemList[k].field] = { label: value.itemList[k].label, message: value.itemList[k].message };
                    // if(k===1){
                    //     this.FormGroup.get(itemList.field).setValidators(this.rangeValidator(value));
                    // }
                }
                if (value.itemObj && value.itemObj.field) {
                    this.FormGroup.addControl(value.itemObj.field, new FormControl(value.itemObj.value, this.validators(value.itemObj)));
                    this.getPickerLabel(value.itemObj);
                }
            } else if (value.type === 'dropDownSelect' && value.writable) {
                this.FormGroup.addControl(value.field, new FormControl(value.value, this.validators(value)));
                this.FormGroup.addControl(`customstyle__${value.itemObj.field}`, new FormControl(value.itemObj.value));
                value.dropDownList.dropDownItem.push({ code: '-1', name: '自定义' });
                if (value.value === '-1' && value.itemObj) {
                    this.FormGroup.get(`customstyle__${value.itemObj.field}`).setValidators(this.validators(value.itemObj));
                    this.FormGroup.get(`customstyle__${value.itemObj.field}`).updateValueAndValidity();

                } else {
                    this.FormGroup.get(`customstyle__${value.itemObj.field}`).clearValidators();
                    this.FormGroup.get(`customstyle__${value.itemObj.field}`).updateValueAndValidity();
                }
                this.hiddenLabel[value.field] = { label: value.label, message: value.message };
                this.hiddenLabel[`customstyle__${value.itemObj.field}`] = { label: value.itemObj.label, message: value.itemObj.message }; ;
            } else if (value.type === 'dropDownBox') {
                value.itemList = [];
                // let thisValue = value.value?(value.value.substr(0, 2).indexOf('#') > -1? '' : value.value):'' ;
                this.getDropDownData(value);
                // this.getPickerLabel(value);
                this.FormGroup.addControl(value.field, new FormControl(value.value, this.validators(value)));
                this.hiddenLabel[value.field] = { label: value.label, message: value.message };
            } else {
                // let thisValue = value.value?(value.value.substr(0, 2).indexOf('#') > -1? '' : value.value):'' ;
                this.FormGroup.addControl(value.field, new FormControl(value.value, this.validators(value)));
                this.hiddenLabel[value.field] = { label: value.label, message: value.message };
            }
        }
        console.log(this.FormGroup, 'hideFFffff');
    }

    ngAfterViewInit() {
    }

    changeValue(event, item) {
        if (event.detail.value.length > Number(item.maxLength)) {
            item.value = event.detail.value.slice(0, Number(item.maxLength));
            this.FormGroup.patchValue({[item.field]: item.value})
        }
    }

    rangeValidator(item): ValidatorFn {
        return (control: AbstractControl): { [key: string]: boolean } | null => {
            const minControl = this.FormGroup.get(item.itemList[0].field);
            const maxControl = this.FormGroup.get(item.itemList[1].field);
            if (!maxControl.value && item.itemList[1].isRequire) {
                return { required: true };
            }
            if (minControl.value && minControl.value >= maxControl.value) {
                return { 'rangeError': false };
            }
        };
        return null;
    }

    changeItemobjValidator(item, type) {
        if (type === 'toggle' && item.writable) {
            item.value = !item.value;
            if (item.value && item.itemObj) {
                const rangeLen = item.itemObj.itemList.length;
                for (let k = 0; k < rangeLen; k++) {
                    const itemListk = item.itemObj.itemList[k];
                    this.FormGroup.get(item.itemObj.itemList[k].field).setValidators(this.validators(itemListk));
                    this.FormGroup.get(item.itemObj.itemList[k].field).updateValueAndValidity();
                    // if(k===1){
                    //         this.FormGroup.get(item.itemObj.itemList[k].field).setValidators(this.rangeValidator(item.itemObj));
                    // }
                    this.hiddenLabel[item.itemObj.itemList[k].field] = { label: item.itemObj.itemList[k].label, message: item.itemObj.itemList[k].message };
                }
            } else {
                const rangeLen = item.itemObj.itemList.length;
                for (let k = 0; k < rangeLen; k++) {
                    const itemListk = item.itemObj.itemList[k];
                    this.FormGroup.get(itemListk.field).clearValidators();
                    this.FormGroup.get(item.itemObj.itemList[k].field).updateValueAndValidity();
                }
            }
        } else if (type === 'select' && item.writable) {
            if (item.value === '-1' && item.itemObj) {
                this.FormGroup.get(`customstyle__${item.itemObj.field}`).setValidators(this.validators(item.itemObj));
                this.FormGroup.get(`customstyle__${item.itemObj.field}`).updateValueAndValidity();

            } else {
                this.FormGroup.get(`customstyle__${item.itemObj.field}`).clearValidators();
                this.FormGroup.get(`customstyle__${item.itemObj.field}`).updateValueAndValidity();
            }
        }
        console.log(this.FormGroup, 'toggllelleelel');
    }

    validators(item) {
        const validators = [];
        if (item.isRequire) {
            validators.push(Validators.required);
        }
        if (item.rule) {
            validators.push(Validators.pattern(item.rule));
        }
        if (item.maxLength) {
            validators.push(Validators.maxLength(item.maxLength));
        }
        if (item.minLength) {
            validators.push(Validators.minLength(item.minLength));
        }
        return validators;
    }

    getErrors(form) {
        const errs = [];
        for (const name in form['controls']) {
            if (form['controls'][name].errors) {
                errs.push({ key: [name], err: form['controls'][name].errors });
            }
        }
        if (errs.length) {
            const name = errs[0];
            console.log(this.hiddenLabel[name.key], 'kkkkkkkkkkk');
            if (name.err.required != undefined) {
                this.appGlobalService.showToast(`请输入${this.hiddenLabel[name.key].label}`, 3000, 'top');
            } else if (name.err.pattern != undefined) {
                this.appGlobalService.showToast(this.hiddenLabel[name.key].message, 3000, 'top');
            } else {
                this.appGlobalService.showToast(`请输入正确的${this.hiddenLabel[name.key].label}`, 3000, 'top');
            }
        }
        console.log(errs, 'errorsssssssssss');
    }

    dismiss() {
        console.log(this.FormGroup, 'FFFFF');
        if (this.FormGroup.invalid) {
            this.getErrors(this.FormGroup);
            return;
        }
        this.modalCtrl.dismiss({ form: this.FormGroup, item: this.data });
    }

    getPickerLabel(item) {
        if (item.value) {
            item.itemList.forEach(element => {
                if (!element.children || !element.children.length) {
                    if (element.code == item.value) {
                        item.checkLabel = element.label;

                    }
                } else {
                    element.children.forEach(ele => {
                        if (ele.code == item.value) {
                            item.checkLabel = ele.label;
                        }
                    });
                }
            });
        } else {
            item.checkLabel = item.hint;
        }
    }

    getDropDownData(item) {
        const table = {
            'table': item.table
        };
        // if (city && item.table.name==='district') { table.table.condition[0].valueStr = city; }
        const params = {
            content: table,
            id: this.data['id'],
            sessionId: localStorage.getItem('sessionId')
        };
        // 请求获取新数据
        this.http.post('application/submit', params, res => {
            // 请求数据成功
            if (res.data.data) {
                item.itemList = res.data.data;
                this.getPickerLabel(item);
            } else {
                // 请求数据失败
                this.appGlobalService.showToast(res['message'], 2000, 'middle');
                this.getPickerLabel(item);
            }
        });
    }

    onOk1(result, item) {
        if (result && result.length && isArray(result)) {
            item.itemObj.value = result[0].code;
            item.itemObj.checkLabel = `${result[0].label}`;
        }
    }

    cancel() {
        this.modalCtrl.dismiss();
    }

}
