    getErrors(form) {
        const errs = [];
        for (const name in form['controls']) {
            if (form['controls'][name].errors) {
                errs.push({ key: [name], err: form['controls'][name].errors });
            }
        }
        if (errs.length) {
            const name = errs[0];
            console.log(this.allLabel[name.key], 'kkkkkkkkkkk');
            if (name.err.required !== undefined) {
                this.appGlobalService.showToast(`请输入${this.allLabel[name.key].label}`, 3000, 'top');
            } else if (name.err.pattern !== undefined) {
                this.appGlobalService.showToast(this.allLabel[name.key].message, 3000, 'top');
            } else if (name.err.rangeError !== undefined) {
                const label = this.allLabel[name.key].label.slice(-2);
                this.appGlobalService.showToast(`${this.allLabel[name.key].label}不得小于/等于最小${label}`, 3000, 'top');
            } else {
                this.appGlobalService.showToast(`请输入正确的${this.allLabel[name.key].label}`, 3000, 'top');
            }
        }
    }

