    takePicture(source) {
        console.log(' message-----> 358 begin takePicture ');
        const that = this;
        const options: CameraOptions = {
            quality: 50,
            sourceType: 1,
            destinationType: this.camera.DestinationType.FILE_URI,
            encodingType: this.camera.EncodingType.JPEG,
            mediaType: this.camera.MediaType.PICTURE,
            allowEdit: true,
            saveToPhotoAlbum: false,
        };
        this.camera.getPicture(options).then((imagePath) => {
            that.startUpload(imagePath);
        }, (err) => {
            console.log(' error-----> 372 ' + JSON.stringify(err));
        });
    }

    startUpload(filePath) {
        this.showLoader();
        this.file.resolveLocalFilesystemUrl(filePath)
            .then(entry => {
                (<FileEntry> entry).file(file => this.readFile(file));
            })
            .catch(err => {
                console.log(' error-----> 383 ' + JSON.stringify(err));
            });
    }

    readFile(file: any) {
        console.log(' message-----> 389 begin readFile ');
        const that = this;
        const reader = new FileReader();
        reader.onloadend = () => {
            const formData = new FormData();
            const imgBlob = new Blob([reader.result], {
                type: file.type
            });
            formData.append('file', imgBlob, file.name);
            that.http.img_post('file/simpleUpload', formData, res => {
                that.hideLoader();
                const url = res.url.replace('https', 'http');
                const newEntry = {
                    name: file.name,
                    path: url,
                    filePath: url
                };
                that.img.push(newEntry);
                that.openImageModal();
            }, (error) => {
                console.log(' error-----> 408 readFile ' + JSON.stringify(error));
            });
        };
        reader.readAsArrayBuffer(file);
    }

    openImgPicker() {
        const that = this;
        this.imageResponse = [];
        this.imagePicker.hasReadPermission()
            .then((result) => {
                if (result === false) {
                    that.imagePicker.requestReadPermission().then(res => {
                        console.log(' message-----> 504 requestReadPermission is  ' + JSON.stringify(res));
                    }, (err) => {
                        console.log(' error-----> 575 ' + JSON.stringify(err));
                    });
                } else if (result === true) {
                    that.imagePicker.getPictures({
                        maximumImagesCount: 10,
                        width: 1024,
                        height: 1024,
                        quality: 100,
                        outputType: 0
                    }).then(
                        (results) => {
                            that.isCompleteBatchUpload = results.length;
                            if (results.length > 0) {
                                that.showLoader();
                                for (let i = 0; i < results.length; i++) {
                                    that.file.resolveLocalFilesystemUrl(results[i])
                                        .then(async entry => {
                                            (<FileEntry> entry).file(file => that.readFileForBatch(file, i, results[i]));
                                        })
                                        .catch(async err => {
                                            console.log(' error-----> 596 ' + JSON.stringify(err));
                                        });
                                }
                            }
                        }, (err) => {
                            console.log(' error-----> 601 ' + JSON.stringify(err));
                        }
                    );
                }
            }, (err) => {
                console.log(' error-----> 606 ' + JSON.stringify(err));
            });
    }

    readFileForBatch(file: any, countNum: any, item: any) {
        const that = this;
        console.log(' message-----> 456 begin readFileForBatch that.isCompleteBatchUpload is' + that.isCompleteBatchUpload);
        const reader = new FileReader();
        reader.onloadend = () => {
            const formData = new FormData();
            const imgBlob = new Blob([reader.result], {
                type: file.type
            });
            formData.append('file', imgBlob, file.name);
            that.http.img_post('file/simpleUpload', formData, res => {
                console.log(' message-----> 466 upload img[' + (countNum + 1) + '] success ');
                const url = res.url.replace('https', 'http');
                const newEntry = {
                    name: file.name,
                    path: url
                };
                that.img.push(newEntry);
                if (that.img.length === that.isCompleteBatchUpload) {
                    that.hideLoader();
                    that.openImageModal();
                }
            }, (error) => {
                console.log(' error-----> img_post： ' + JSON.stringify(error));
            });
        };
        reader.readAsArrayBuffer(file);
    }

    createFileName() {
        const d = new Date(),
            n = d.getTime(),
            newFileName = n + '.jpg';
        return newFileName;
    }

    uploadFileForWeb(event: any) {
        const that = this;
        if (event.target.files && event.target.files[0]) {
            this.selectImg = event.target.files[0];
            if (this.selectImg.size > 1024 * 1024 * 5) {
                this.appGlobalService.showToast('图片不能大于5M，请重新选择', 3000, 'middle');
                return;
            }
            if (['jpeg', 'png', 'gif', 'jpg'].indexOf(this.selectImg.type.split('/')[1]) > -1) {
                const reader = new FileReader();
                reader.readAsDataURL(that.selectImg);
                reader.onload = function (e) {
                    const uploadData = new FormData();
                    uploadData.append('file', that.selectImg);
                    that.http.img_post('file/simpleUpload', uploadData, res => {
                        let url = JSON.stringify(res.url).toString().replace('https', 'http');
                        console.log('message----->' + url);
                        url = url.replace('"', '').replace('"', '');
                        that.img.push({ 'name': that.selectImg.name, path: url }); // update by andrew.wen for test
                        that.openImageModal();
                    });
                };
            } else {
                this.appGlobalService.showToast('格式不正确，请选择图片', 3000, 'middle');
            }
        }
    }

