    async openHiddenEle(item) {
        const thisItem = item;
        const that = this;
        const id = this.tagData['clickKey'];
        const params = { item: item, id: id };
        const modal = await this.modalCtrl.create({
            component: hiddeneleComponent,
            componentProps: { value: JSON.stringify(params) }
        });
        modal.onDidDismiss().then(function (data) {
            if (data.data) {
                const hiddenForm = data.data.form;
                const hiddenValue = hiddenForm.value;

                for (let i = 0; i < item.itemList.length; i++) {
                    const prop = item.itemList[i];
                    that.formGroup.patchValue({ [prop.field]: hiddenValue[prop.field] });
                    prop.value = hiddenValue[prop.field];

                    if (prop.type === 'text' && prop.writable) {
                        that.formGroup.patchValue({ [prop.itemObj.field]: hiddenValue[prop.itemObj.field] });
                        prop.itemObj.value = hiddenValue[prop.itemObj.field];
                    }
                    if (prop.type === 'dropDownSelect' && prop.writable) {
                        that.formGroup.patchValue({ [prop.itemObj.field]: hiddenValue[prop.itemObj.field] });
                        that.formGroup.patchValue({ [`customstyle__${prop.itemObj.field}`]: hiddenValue[`customstyle__${prop.itemObj.field}`] });
                        prop.itemObj.value = hiddenValue[`customstyle__${prop.itemObj.field}`];
                    }

                    if (prop.type === 'toggle' && prop.writable) {
                        if (!prop.both) {
                            prop.value = hiddenValue[`customToggle__${prop.field}`];
                        } else {
                            prop.value = hiddenValue[prop.field];
                        }

                        for (let j = 0; j < prop.itemObj.itemList.length; j++) {
                            that.formGroup.patchValue({ [prop.itemObj.itemList[j].field]: hiddenValue[prop.itemObj.itemList[j].field] });
                            prop.itemObj.itemList[j].value = hiddenValue[prop.itemObj.itemList[j].field];
                        }
                        if (prop.itemObj.itemObj) {
                            that.formGroup.patchValue({ [prop.itemObj.itemObj.field]: hiddenValue[prop.itemObj.itemObj.field] });
                            prop.itemObj.itemObj.value = hiddenValue[prop.itemObj.itemObj.field];
                        }
                        if (prop.type === 'range') {
                            for (let j = 0; j < prop.itemList.length; j++) {
                                that.formGroup.patchValue({ [prop.itemList[j].field]: hiddenValue[prop.itemList[j].field] });
                                prop.itemList[j].value = hiddenValue[prop.itemList[j].field];
                            }
                            if (prop.itemObj) {
                                that.formGroup.patchValue({ [prop.itemObj.field]: hiddenValue[prop.itemObj.field] });
                                prop.itemObj.value = hiddenValue[prop.itemObj.field];
                            }
                        }
                    }
                }
            }
            console.log(that.formGroup, 'hidddd')
        });
        return await modal.present();
    }

