    pushTagToContainer(comContainer: any, footerContainer: any, style: any, tag: any, searchInputParams?) {
        this.getComsDataByStyle(style, data => {
            if (data) {
                comContainer.push(data);
            }
        }, tag, searchInputParams);
    }

