import { Component, OnInit, Input } from '@angular/core';
import { ModalController } from '@ionic/angular';
import {AppGlobalService} from '../../shared/service/app-global.service';
import { HttpService } from '../../shared/service/http.service';
import { ChatonlineComponent } from '../chatonline/chatonline.component';
import { EventService } from '../../shared/service/event.service';
// import { Socket } from 'ng-socket-io';

@Component({
  selector: 'app-chat-invite',
  templateUrl: './chat-invite.component.html',
  styleUrls: ['./chat-invite.component.scss'],
})
export class ChatInviteComponent implements OnInit {

  constructor(private appGlobalService: AppGlobalService, 
              private http: HttpService, 
              private modalCtrl: ModalController,
              private eventService: EventService
            //   private socket: Socket
              ) { }

  @Input() tagData: any;

pageNo: any = 1;
params: any;
sessionId = localStorage.getItem('sessionId');

  ngOnInit() {
    console.log(this.tagData, 'tag');
    this.params = this.tagData.data.submit;
  }

  async openModal(item){
    const key = localStorage.getItem('tab');
    this.eventService.eventEmit.emit("badgeForTab", {[key]: item.badge});
    item.badge = 0;
    // this.socket.connect();
    // this.socket.emit('set-nickname', item.name);
    const modal = await this.modalCtrl.create({
        component: ChatonlineComponent,
        componentProps: {value: item }
    });
    modal.onDidDismiss().then(function(data) {

    });
    return await modal.present();
  }
}
