import { Component, OnInit } from '@angular/core';
import { ModalController, NavParams } from '@ionic/angular';
import { AppGlobalService } from '../../shared/service/app-global.service';
@Component({
  templateUrl: 'editnickname.page.html',
  styleUrls: ['./edit.component.scss']
})
export class EditNickNameComponent implements OnInit {
  name: any;
  currentImgIndx: any;
  constructor(private modalController: ModalController,
    private navParams: NavParams, private appGlobalService: AppGlobalService
  ) {

  }
  tmp_nickname: any;

  ngOnInit() {
    this.tmp_nickname = this.navParams.data.value;
    this.name = this.navParams.data.value;
  }

  goBack() {
    this.modalController.dismiss(this.tmp_nickname);
  }

  doSave() {
    const pattern = new RegExp('^[\u4E00-\u9FA5A-Za-z0-9]+$');
    if (this.name == undefined || this.name == '' || JSON.stringify(this.name).trim().length == 0) {
      this.appGlobalService.showToast('请输入昵称', 2000, 'middle');
      return;
    } else { 
      if(pattern.test(this.name)){
        this.modalController.dismiss(this.name);
      }else{
        this.appGlobalService.showToast('只能输入中文、英文、数字。', 2000, 'middle');
        return;
      }
      
    }

  }

}
