import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { AlertController, LoadingController } from '@ionic/angular';

import { callbackify } from 'util';

import { EventService } from 'src/app/shared/service/event.service';

import { AppGlobalService } from './app-global.service';
import { TabsService } from './tabs.service';

@Injectable({
    providedIn: 'root'
})
export class HttpService {

    // bathUrl = 'http://zeno.xwkj.local:9005/openapi/'; // 测试环境api接口路径
    // bathUrl = 'http://zeno-performance-gateway.xwkj.local/openapi/'; // 性能环境api接口路径
    bathUrl = 'https://zeno-api.xw18.cn/openapi/'; // 线上环境api接口路径
    // bathUrl = 'http://zeno-dev-gateway.xwkj.local/openapi/'; // 开发环境

    headers: any;
    loadingIsOpen: any;
    loading: any;

    constructor(
        private http: HttpClient,
        public eventService: EventService,
        private loadingCtrl: LoadingController,
        private appGlobalService: AppGlobalService,
        private alertController: AlertController) {
    }

    setHeasers() {
        this.headers = new HttpHeaders({ 'Content-Type': 'application/json;charset=UTF-8' });
    }

    handleError(error) {
        if (error.error && error.error.code === '1000') {
            console.log('error----->' + JSON.stringify(error));
            this.checkBack();
        } else if (error.error && error.error.code === '5505') {
            this.checkSession();
        } else {
            this.checkBack();
        }
    }

    post(path: string, params: any, callback: Function, err?: Function) {
        const url = this.bathUrl + path;
        this.setHeasers();
        if (params.pageId || params.viewId) {
            this.showLoading('');
        }
        return this.http.post(url, params, { headers: this.headers })
            .subscribe(data => {
                this.hideLoading();
                callback(data);
            }, error => {
                this.hideLoading();
                this.handleError(error);
                if (err) {
                    err(error.error);
                }
            });
    }

    img_post(path: string, params: any, callback: Function, err?: Function) {
        const url = this.bathUrl + path;
        return this.http.post(url, params)
            .subscribe(data => {
                callback(data);
            }, error => {
                this.handleError(error);
                if (err) {
                    err(error.error);
                }
            });
    }

    get(path: string, callback: Function, err?: Function) {
        const url = this.bathUrl + path;
        this.showLoading('');
        this.setHeasers();

        return this.http.get(url, { headers: this.headers })
            .subscribe((data) => {
                this.hideLoading();
                callback(data);
            }, (error) => {
                this.hideLoading();
                if (err) {
                    this.handleError(error);
                    err(error);
                }
            });
    }

    // getLocationAddress(latitude: any, longitude: any, callback: Function, err?: Function) {
    //     // tslint:disable-next-line:max-line-length
    // const url = this.baiduAPIUrl
    // + 'ak=sIq3pmhG5n4xVuKQ8eKr1BiV0hsLP2ek&renderReverse&location=' + latitude + ',' + longitude + '&output=json&pois=1';
    //     this.setHeasers();
    //     return this.http.get(url, { headers: this.headers })
    //         .subscribe((data) => {
    //             callback(data);
    //         }, (error) => {
    //             if (err) {
    //                 this.handleError(error);
    //                 err(error);
    //             }
    //         });
    // }


    delete(path: string, params: any, callback: Function, err?: Function) {
        const url = this.bathUrl + path;
        this.setHeasers();
        this.showLoading();
        return this.http.delete(url, { params, headers: this.headers })
            .subscribe(data => {
                this.hideLoading();
                callback(data);
            }, error => {
                this.hideLoading();
                if (err) {
                    this.handleError(error);
                    err(error.error);
                }
            });
    }

    put(path: string, params: any, callback: Function, err?: Function) {
        const url = this.bathUrl + path;
        this.showLoading('');
        this.setHeasers();
        return this.http.put(url, params, { headers: this.headers })
            .subscribe(data => {
                this.hideLoading();
                callback(data);
            }, error => {
                this.hideLoading();
                if (err) {
                    this.handleError(error);
                    err(error.error);
                }
            });
    }

    getSession(callback: Function) {

        if (this.bathUrl.indexOf('zeno-api.xw18.cn') !== -1) {
            this.get('application/openApp/xw', ress => {
                localStorage.setItem('sessionId', ress.sessionId);
                console.log('new session-------->\n', ress.sessionId);
                callback(ress.sessionId);
            }, err => {
            });
        } else if (this.bathUrl === 'http://zeno.xwkj.local:9005/openapi/') {
            this.get('application/debug/134', ress => {
                localStorage.setItem('sessionId', ress.sessionId);
                console.log('new session-------->\n', ress.sessionId);
                callback(ress.sessionId);
            }, err => {
            });
        }
    }

    reloadCurrentPage() {
        this.eventService.eventEmit.emit('reloadCurrentPage', 'reloadCurrentPage');
    }

    async checkSession() {
        this.hideLoading();
        const alert = await this.alertController.create({

            header: '',
            subHeader: '提示',
            message: 'session已过期，请刷新页面',
            buttons: [
                {
                    text: '确定',
                    handler: () => {
                        this.getSession(session => {
                            this.reloadCurrentPage();
                        });
                    }
                },]
        });
        await alert.present();
    }

    async checkBack() {
        const that = this;
        // this.hideLoading();
        const alert = await this.alertController.create({

            header: '',
            subHeader: '提示',
            message: '未知错误，请重新进入页面',
            buttons: [
                {
                    text: '确定',
                    handler: () => {
                        console.log('确定，请重新进入页面!!!');
                        that.eventService.eventEmit.emit('backLastPage', 'backLastPage');
                    }
                },]
        });
        await alert.present();
    }

    private async showLoading(contentStr: any = ''): Promise<void> {
        if (typeof contentStr !== 'string') {
            contentStr = '';
        }
        if (!this.loadingIsOpen) {
            console.log('showLoading 被执行~~~');
            this.loadingIsOpen = true;
            const loading = await this.loadingCtrl.create({
                message: '',
                duration: 1000
            });
            await loading.present();
            await loading.onDidDismiss();
            console.log('Loading dismissed!');
        } else {
            console.log('showLoading 没有被执行~~~');
        }
    }

    private async hideLoading() {
        if (this.loadingIsOpen) {
            setTimeout(() => {
                this.loadingIsOpen = false;
            }, 1000);
        }
    }
}
