import { Component, OnInit, Input } from '@angular/core';
import { AppGlobalService } from '../../shared/service/app-global.service';

@Component({
    selector: 'app-counselor',
    templateUrl: './counselor.component.html',
    styleUrls: ['./counselor.component.scss'],
})
export class CounselorComponent implements OnInit {

    @Input() tagData: any;

    showData: any;
    showList = true;

    defaultPhoto: string;

    constructor(
        private appGlobalService: AppGlobalService
    ) {
    }

    ngOnInit() {

        console.log(this.tagData);
        this.defaultPhoto = 'assets/img/head.png';

        /*
        area: "熟悉区域：5ca5b064e2c5f000013da4b9"
        imageUrl: "https://zeno-img.oss-cn-shenzhen.aliyuncs.com/file_e6f513723c5aeed5294133fa023880c6.jpg"
        link: {type: "page", params: {…}, target: "8597"}
        name: "过hjv新的111"
        phone: "18923802558"
        tag: "转店  haha"
        trade: "擅长行业：##title"
        * */
        // console.log(this.tagData);
        if (this.tagData.data.notloginData) {
            this.showList = true;
            this.showData = this.tagData.data.notloginData;
        } else if (this.tagData.data.loginedData) {
            this.showList = false;
            this.showData = this.tagData.data.loginedData;
            if(this.showData.area.indexOf(",") !== -1){
                this.showData.area = this.showData.area.replace(/,/g, '/').substring(0, this.showData.area.length - 1);
            }
            if(this.showData.trade.indexOf(",") !== -1){
                this.showData.trade = this.showData.trade.replace(/,/g, '/').substring(0, this.showData.trade.length - 1);
            }
        }
    }

    go(obj) {
        const pageParams = {
            pageId: obj.target,
            params: obj.params,
            sessionId: localStorage.getItem('sessionId')
        };
        this.appGlobalService.goToForward(pageParams);
    }

}
