import { Component, Input, OnInit } from '@angular/core';
import { AppGlobalService } from '../../shared/service/app-global.service';
@Component({
  selector: 'app-invitation-to-shop',
  templateUrl: './invitation-to-shop.component.html',
  styleUrls: ['./invitation-to-shop.component.scss'],
})
export class InvitationToShopComponent implements OnInit {

  @Input() tagData: any;
  constructor(private globalService: AppGlobalService) { }

  ngOnInit() { }

  gotoInvatation(obj) {
    const pageParams = {
      pageId: obj.target,
      params: obj.params,
      sessionId: localStorage.getItem('sessionId')
    };
    this.globalService.goToForward(pageParams);
  }

}
